const axios_config =
            {
                // `url`  URL

                // `method` 

                // `baseURL`  `url`  `url`  URL
                //  `baseURL`  axios  URL
                // baseURL: 'https://some-domain.com/api/',

                // `transformRequest` 
                //  'PUT', 'POST'  'PATCH' 
                //  ArrayBuffer Stream
                transformRequest: [function (data) {
                    // Do whatever you want to transform the data
                    let ret = ''
                    for (let it in data) {
                        ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
                    }
                    return ret
                }],

                // `transformResponse`  then/catch 
                // transformResponse: [function (data) {
                //     //  data 
                //
                //     return data;
                // }],

                // `headers` 
                headers: {
                    // 'X-Requested-With': 'XMLHttpRequest',
                    'Content-Type':'application/x-www-form-urlencoded'
                },

                // `params`  URL 
                // (plain object) URLSearchParams 
                // params: {
                //     ID: 12345
                // },

                // `paramsSerializer`  `params` 
                // (e.g. https://www.npmjs.com/package/qs, http://api.jquery.com/jquery.param/)
                // paramsSerializer: function(params) {
                //     return Qs.stringify(params, {arrayFormat: 'brackets'})
                // },

                // `data` 
                //  'PUT', 'POST',  'PATCH'
                //  `transformRequest` 
                // - string, plain object, ArrayBuffer, ArrayBufferView, URLSearchParams
                // - FormData, File, Blob
                // - Node  Stream
                // data: {
                //     firstName: 'Fred'
                // },

                // `timeout` (0 )
                //  `timeout` 
                // timeout: 6000,

                // `withCredentials` 
                // withCredentials: false, // 

                // `adapter` 
                //  promise  ( [response docs](#response-api)).
                // adapter: function (config) {
                    /* ... */
                // },

                // `auth`  HTTP 
                //  `Authorization`  `headers`  `Authorization`
                // auth: {
                //     username: 'janedoe',
                //     password: 's00pers3cret'
                // },

                // `responseType`  'arraybuffer', 'blob', 'document', 'json', 'text', 'stream'
                responseType: 'json', // 

                // `xsrfCookieName`  xsrf token cookie
                xsrfCookieName: 'XSRF-TOKEN', // default

                // `xsrfHeaderName`  xsrf token  HTTP 
                xsrfHeaderName: 'X-XSRF-TOKEN', // 

                // `onUploadProgress` 
                // onUploadProgress: function (progressEvent) {
                //     // 
                // },

                // `onDownloadProgress` 
                onDownloadProgress: function (progressEvent) {
                    // 
                },

                // `maxContentLength` 
                // maxContentLength: 2000,

                // `validateStatus` HTTP  resolve  reject  promise  `validateStatus`  `true` ( `null`  `undefined`)promise  resolve; promise  rejecte
                // validateStatus: function (status) {
                //     return status >= 200 && status < 300; // 
                // },

                // `maxRedirects`  node.js  follow 
                // 0 follow 
                // maxRedirects: 5, // 

                // `httpAgent`  `httpsAgent`  node.js  http  https 
                // `keepAlive` 
                // httpAgent: new http.Agent({ keepAlive: true }),
                // httpsAgent: new https.Agent({ keepAlive: true }),

                // 'proxy' 
                // `auth`  HTTP 
                //  `Proxy-Authorization`  `header`  `Proxy-Authorization` 
                // proxy: {
                //     host: '127.0.0.1',
                //     port: 9000,
                //     auth:  {
                //         username: 'mikeymike',
                //         password: 'rapunz3l'
                //     }
                // },

                // `cancelToken`  cancel token
                //  Cancellation 
                // cancelToken: new CancelToken(function (cancel) {
                // })
        }




export {axios_config}



